.box {
  width: 100%;
  height: 100%;
  position: relative;
}

.container {
  white-space: nowrap;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
  width: 100%;
  height: 100%;
  &::-webkit-scrollbar {
    display: none;
  }
}

.element {
  display: inline-block;
  width: 100%;
  height: 100%;
  aspect-ratio: 1;
  text-align: center;
  font-size: 20px;
  scroll-snap-align: center;

  img {
    width: 100%;
    height: 100%;
  }
  video {
    width: 100%;
    height: 100%;
  }
}

.scroll {
  position: absolute;
  top: 50%;
  padding: 0.5rem;
  font-size: 1.5rem;
  border: none;
  cursor: pointer;
  height: 2rem;
  width: 2rem;
  background-size: cover;
}
.scroll-left {
  left: 0.8rem;
}

.scroll-right {
  right: 0.8rem;
  transform: rotate(180deg);
}
